.demo-page-container{
  // 使其不被隔离化 https://blog.csdn.net/weixin_42436131/article/details/124388091
  :global(.ant-pro-page-container-children-content ) {
    padding-block: 0;
    padding-inline: 0;
  }
  :global(.w-e-bar),
  :global(.w-e-text-container) {
    background-color: transparent;
  }
  :global(.editor) {
    min-height: calc(100vh - 200px)
  }
  :global(#w-e-textarea-1),
  :global(.w-e-text-placeholder) {
    margin: auto;
    width: 1200px;
    padding: 0;
  }
  :global(.w-e-text-placeholder) {
    left: 0px;
    right: 0px;
    bottom: 0px;
    font-style: normal;
  }
}
.create-form {
  width: 400px;
  :global(.ant-space) {
    display: flex;
    justify-content: end;
  }
}
.comupload {
  :global(.ant-upload-select) {
    padding-bottom: 66%;
    width: 66% !important;
  }
}
